.table-container {
  padding-top: 15vh;
}

.users-table {
  margin: 0 auto;
  width: 70%;
  border-collapse: collapse;
  background: rgb(255, 255, 255);
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);

  tr {
    height: 100%;
  }

  th,
  td {
    width: 25%;
    height: 100%;
    padding: 10px;
    color: white;

    &:last-child {
      border-right: none;
    }
  }


  &__head {
    width: 100%;

    &__titles {
      background: rgb(21, 29, 56);
      border-bottom: 1px solid rgb(34, 47, 88);
      text-transform: uppercase;
      font-weight: 400;

      th {
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 2px;
        border-right: 1px solid rgb(114, 114, 114);
      }
    }

    &__filters {
      background: rgb(153, 153, 153);
      border-bottom: 2px solid #ffffff;

      th {
        border-right: 1px solid rgb(114, 114, 114);
        text-align: left;
        position: relative;
      }

      &__item {
        width: 90%;
        height: 20px;
        font-size: 16px;
        color: rgb(255, 255, 255);

        &::placeholder {
          color: rgb(216, 216, 216);
        }

        
      }

      &__reset-btn {
        border: 1px solid rgb(197, 197, 197);
        position: absolute;
        right: 6px;
        font-size: 12px;
        padding: 3px 6px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  &__body {
    tr:hover {
      background-color: rgb(233, 238, 255);
    }

    tr:nth-child(even) {
      background-color: rgb(240, 240, 240);

      &:hover {
        background-color: rgb(220, 226, 240);
      }
    }

    td {
      padding: 5px;

      span:not(
          .users-table__body__without-span-style,
          .users-table__body__ext
        ) {
        display: inline-flex;
        align-items: flex-end;
        padding: 8px 10px;
        width: 100%;
        color: rgb(27, 27, 27);
        font-size: 16px;
        border: 1px solid rgb(197, 197, 197);

        b {
          color: rgb(21, 29, 56);
          padding: 0 1px;
        }
      }
    }
    &__ext {
      font-size: 14px;
      padding-left: 10px;
    }
  }
}
